"use strict";
$(document).ready(function () {
    transformHeaderBg();
    cookieContainer();
    var headerHeight = $('.header').height();
    $('.hero-text').css('margin-top', headerHeight);
    $('.navbar-toggler').click(function () {
        $('.header').toggleClass('active-header');
        $('.icon-menu').toggleClass('active');
    });
    $('#top').click(function () {
        $('html, body').animate({ scrollTop: 0 }, 'slow');
        if ($('.active-header').length && $('.active').length) {
            $('.header').toggleClass('active-header');
            $('.icon-menu').toggleClass('active');
            $('.collapse').removeClass('show');
        }
    });
    $('.navbar-nav li a, .scroll').click(function () {
        var el = $(this).attr('href');
        var elWrapped = $(el);
        scrollToDiv(elWrapped, 160);
        if ($('.active-header').length && $('.active').length) {
            $('.header').toggleClass('active-header');
            $('.icon-menu').toggleClass('active');
            $('.collapse').removeClass('show');
        }
        return false;
    });
    $('#toggle-cookie-settings').on('click', function () {
        $('#cookie-notice').show();
    });
    $('#cookie-close').on('click', function () {
        $('#cookie-notice').hide();
    });
    function getCookie(key) {
        var keyValue = document.cookie.match('(^|;) ?' + key + '=([^;]*)(;|$)');
        return keyValue ? keyValue[2] : null;
    }
    function cookieContainer() {
        var cookieSetting = getCookie('ga-disable-UA-158821201-1');
        if (cookieSetting && cookieSetting === 'true') {
            // user does not want Google Analytics
            $('#gehtKlar').prop('checked', false);
            $('#gehtNichtKlar').prop('checked', true);
            $('#cookie-notice').hide();
            console.log('Cookie value is ' +
                cookieSetting +
                ' ' +
                'User does not want Google Analytics');
        }
        else if (cookieSetting && cookieSetting === 'false') {
            // user has agreed to Google Analytics
            $('#gehtKlar').prop('checked', true);
            $('#gehtNichtKlar').prop('checked', false);
            $('#cookie-notice').hide();
            console.log('Cookie value is ' +
                cookieSetting +
                ' ' +
                'User has agreed to Google Analytics');
        }
        else {
            $('#gehtKlar').prop('checked', false);
            $('#gehtNichtKlar').prop('checked', true);
            $('#cookie-notice').show();
            console.log('Cookie is null. User is not being tracked!');
        }
    }
    function scrollToDiv(element, navheight) {
        var offset = element.offset();
        var offsetTop = offset.top;
        var totalScroll = offsetTop - navheight;
        $('body,html').animate({
            scrollTop: totalScroll
        }, 500);
    }
    window.onscroll = function () {
        scrollFunction();
    };
    function transformHeaderBg() {
        var heroHeight = $('.hero').height();
        if (document.body.scrollTop > 300 ||
            document.documentElement.scrollTop > 300) {
            $('.header').css('background', 'rgba(36,36,36)');
            $('.logo').css('height', '60px');
        }
        else {
            $('.header').css('background', 'rgba(36,36,36,0.4)');
            $('.logo').css('height', '80px');
        }
        if (document.body.scrollTop > heroHeight ||
            document.documentElement.scrollTop > heroHeight) {
            $('#top')
                .show()
                .fadeIn('slow');
        }
        else {
            $('#top')
                .attr('style', 'display: none !important')
                .fadeOut('slow');
        }
    }
    function scrollFunction() {
        transformHeaderBg();
    }
    var frm = $('#contact-form');
    frm.submit(function (e) {
        e.preventDefault();
        var validForm = validateContact();
        if (validForm) {
            $.ajax({
                type: frm.attr('method'),
                url: frm.attr('action'),
                data: frm.serialize(),
                success: function (data) {
                    $('.success-messages').show();
                    console.log('Submission was successful.');
                    console.log(data);
                },
                error: function (data) {
                    $('.unsuccess-messages').show();
                    console.log('An error occurred.');
                    console.log(data);
                }
            });
        }
        else {
            console.log('An error occurred. Invalid form');
        }
    });
    function validateContact() {
        var valid = true;
        $('.form-control').css('background-color', '');
        $('.info').html('');
        if (!$('#nameInput').val()) {
            $('#userName-info').html(' (nötig)');
            $('#nameInput').css('background-color', '#FFFFDF');
            valid = false;
        }
        if (!$('#emailInput').val()) {
            $('#userEmail-info').html(' (nötig)');
            $('#emailInput').css('background-color', '#FFFFDF');
            valid = false;
        }
        // if (!<any>$("#emailInput").val()!.match(/^([\w-\.]+@([\w-]+\.)+[\w-]{2,4})?$/)) {
        //   $("#userEmail-info").html("(invalid)");
        //   $("#emailInput").css('background-color', '#FFFFDF');
        //   valid = false;
        // }
        if (!$('#subjectInput').val()) {
            $('#subject-info').html(' (nötig)');
            $('#subjectInput').css('background-color', '#FFFFDF');
            valid = false;
        }
        if (!$('#messageInput').val()) {
            $('#message-info').html(' (nötig)');
            $('#messageInput').css('background-color', '#FFFFDF');
            valid = false;
        }
        return valid;
    }
});
